	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Master Builder","date":"02 Apr 2003","slug":"/2003-04-02-master-builder/","tags":"Y2003"},"html":"<p>One more before we go. Christopher was impresssed by the S-Bahn in Berlin, so impressed that last night he built his own version out of Legos, all by himself.</p>\n<p><img alt=\"crhsbahn.jpg\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2003/04/crhsbahn.jpg\" width=\"325\" height=\"244\" border=\"0\" /></p>\n<p>He can even smile, if you ask him to.</p>\n<p><img alt=\"crhsbahn2.jpg\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2003/04/crhsbahn2.jpg\" width=\"244\" height=\"325\" border=\"0\" /></p>"},"next":{"fields":{"title":"We're Off","date":"01 Apr 2003","slug":"/2003-04-01-were-off/"}},"previous":{"fields":{"title":"Rockaway Beach","date":"04 Apr 2003","slug":"/2003-04-04-rockaway-beach/"}}},{"node":{"fields":{"title":"We're Off","date":"01 Apr 2003","slug":"/2003-04-01-were-off/","tags":"Y2003"},"html":"<p>Well, actually, we're leaving tomorrow for Floida, but the computers at home are getting turned off now. Maybe I'll post from Florida, maybe I'll even post from the flight over (we're taking the Lufthansa route where inflight WiFi is being tested), or maybe I won't post at all until Easter when we return.</p>"},"next":{"fields":{"title":"Children's Stories","date":"30 Mar 2003","slug":"/2003-03-30-childrens-stories/"}},"previous":{"fields":{"title":"Master Builder","date":"02 Apr 2003","slug":"/2003-04-02-master-builder/"}}},{"node":{"fields":{"title":"Children's Stories","date":"30 Mar 2003","slug":"/2003-03-30-childrens-stories/","tags":"Y2003"},"html":"<p>I've been refraining from commenting on the war since it started, since I'm assuming that 90% of what the media is reporting is wrong and I don't know which 10% is right. And frankly, at 7 pm when I have the choice between the nightly news on ZDF and Christopher's favorite cartoon on the Kinderkanal, I know which one we pick. I would appreciate some simple stories like the ones <a title=\"Bjørn Stærk blog\" href=\"http://www.bearstrong.net/warblog/000138.html\">Bjørn Stærk</a> mentioned from a newspaper in Norway, \"without that compulsive need to put everything in perspective. Perspective is fine, but worthless unless you have something to, you know, <em>put in perspective</em>.\"</p>\n<p>(Perhaps another reason is that I've been mentally on vacation for the past two weeks, even though we don't leave until Wednesday. We'll be spending two weeks on the Gulf coast in Florida, in the shadow of <a href=\"http://www.centcom.mil/aboutus/centcom.htm\">US Central Command</a>. I imagine it will be one of the safest places on earth.)</p>"},"next":{"fields":{"title":"I Was On TV!","date":"29 Mar 2003","slug":"/2003-03-29-i-was-on-tv/"}},"previous":{"fields":{"title":"We're Off","date":"01 Apr 2003","slug":"/2003-04-01-were-off/"}}}],"pathPrefix":"","first":false,"last":false,"index":773,"pageCount":1101}}

